			<div slot="footer">
              <b-button type="submit" size="sm" variant="primary" v-text="$ml.get('submit')"><i class="fa fa-dot-circle-o"></i></b-button>
	        </div>
		</b-form>
          	</b-tab>
		</b-tabs>
	</b-card>
	</b-col>
	</b-row>
	<b-modal size="xl" class="modal-primary" v-model="confirmModal" @ok="confirmModal" hide-footer>
		<template slot="modal-header">
	      <h5 v-text="modaltitle[0].name"></h5>
	      <div style="display:inline-flex">
	      	<ejs-tooltip position="TopCenter" :content="$ml.get('warncarryover')">
	      		<b-button type="submit" size="sm" variant="primary" v-text="$ml.get('confirm')" @click="carryOverConfirm"></b-button>
	  		</ejs-tooltip>
	      <b-button class="bg-transparent" style="border:none" type="submit" size="sm" @click="confirmModal = false" variant="secondary"><i class="icon-close font-2xl"></i></b-button>
	    </div>
    </template>
		<!-- <b-table responsive="xl" striped hover :items="alteredhead">
		</b-table> -->
		<div class="control-section">
		<ejs-grid ref='overviewgrid' height="auto" :showColumnChooser="true" :rowHeight='20' :allowResizing='true' :allowReordering='true'  id='overviewgrid' :pageSettings="pageSettings" :allowPaging="true" :allowPdfExport="true" :allowExcelExport="true" :toolbar="toolbar" :dataSource="alteredhead" :allowFiltering='false'  :allowSelection='true' :allowSorting='false' :enableHover='false'> 
                <e-columns>
                    <e-column field="name" headerText='Head' :width='130'></e-column>
                    <e-column field="new_remaining_a" :headerText="getRemainingA" format="C0" :width='200'></e-column>
                    <e-column field="new_remaining_b" :headerText="getRemainingB" format="C0" :width='200'></e-column>
                    <e-column field="new_total_b" :headerText="getTotalB" format="C0" :width='170'></e-column>
                </e-columns>
                </ejs-grid>
                </div>
                <b-modal size="sm" :title="$ml.get('warning')" class="modal-primary" v-model="warnModal" @ok="sendReqCarryOver">
                	<h5><span v-text="$ml.get('confirmcarryover')"></span> : {{modaltitle[0].name}} <span v-text="$ml.get('budget')"></span> ?</h5>
                </b-modal>
	</b-modal>
	</div>
</template>
<script>
	import Vue from 'vue' 
import moment from 'moment'

	import { CoolSelect } from 'vue-cool-select'
	import Treeselect from '@riophae/vue-treeselect'
	import { DropDownList, DropDownListPlugin} from '@syncfusion/ej2-vue-dropdowns';
	import axios from 'axios'
	import apiUrl from '@/apiUrl'
	import {Aggregate,PdfExport,ExcelExport, Edit, ColumnMenu, Toolbar, Resize, ColumnChooser, Page, GridPlugin, VirtualScroll, Sort, Filter, Selection, GridComponent,Reorder } from "@syncfusion/ej2-vue-grids";
	import '@riophae/vue-treeselect/dist/vue-treeselect.css'
	import { TextBoxPlugin } from '@syncfusion/ej2-vue-inputs';
	import VueNotifications from 'vue-notifications'
import { TooltipPlugin } from "@syncfusion/ej2-vue-popups";

	import miniToastr from 'mini-toastr'// https://github.com/se-panfilov/mini-toastr

	const toastTypes = {
	  success: 'success',
	  error: 'error',
	  info: 'info',
	  warn: 'warn'
	}
    Vue.use(GridPlugin);
    Vue.use(TooltipPlugin);

	miniToastr.init({types: toastTypes})

	function toast ({title, message, type, timeout, cb}) {
	  return miniToastr[type](message, title, timeout, cb)
	}

	const options = {
	  success: toast,
	  error: toast,
	  info: toast,
	  warn: toast
	}
	//  VueNotifications.setPluginOptions(options)

	Vue.use(VueNotifications, options)
	Vue.use(TextBoxPlugin)
	Vue.use(DropDownListPlugin)
	var api = axios.create({
		withCredentials:true
	});
	export default {
		components :{
			Treeselect,
			CoolSelect
		},
		provide: {
	         grid: [Aggregate,PdfExport,ExcelExport,Edit,ColumnMenu,Resize, Filter, Selection, Sort, VirtualScroll,Toolbar, Page,ColumnChooser,Reorder]
	    },
		data: function() {
			return {
				footerMax: function () {
			        return  { template : Vue.component('maxTemplate', {
			            template: `<span>Sum: {{data.Sum}}</span>`,
			            data () {return { data: {}};},
			            mounted() {
			                this.data.Sum = this.changeAmounttoFormat(this.data.Sum)
			            },
			            methods : {
			                changeAmounttoFormat(amount) {
			                    var x=amount;
			                    x=x.toString();
			                    var lastThree = x.substring(x.length-3);
			                    var otherNumbers = x.substring(0,x.length-3);
			                    if(otherNumbers != '')
			                        lastThree = ',' + lastThree;
			                    var res = otherNumbers.replace(/\B(?=(\d{2})+(?!\d))/g, ",") + lastThree;
			                    return `₹ ${res}`
			                },
			            }
			            })
			          }
			      },
			      toolbar: ['Search'],
				head1:{},
				head2:{},
				heads1:[],
				heads2:[],
				headList:[],
				alteredhead:[],
				input:{},
				modaltitle:[{name:""}],
				confirmModal:false,
				input2:{
				},
				warnModal:false,
				filterOptions: {
                    type: 'CheckBox'
                },
                pageSettings: {pageSize:true,pageSize:20, pageSizes: [20,50,100,200,300,400,500], pageCount: 4 },
				months:[ {name:"January",value:"0"},{name:"February",value:"1"},{name:"March",value:"2"},{name:"April",value:"3"},{name:"May",value:"4"},{name:"June",value:"5"},{name:"July",value:"6"},{name:"August",value:"7"},{name:"September",value:"8"},{name:"October",value:"9"},{name:"November",value:"10"},{name:"December",value:"11"},

          		],
          		carry_over_months : [{name:"January --> February",value:"0"},{name:"February --> March",value:"1"},{name:"March --> April",value:"2"},{name:"April --> May",value:"3"},{name:"May --> June",value:"4"},{name:"June --> July",value:"5"},{name:"July --> August",value:"6"},{name:"August --> September",value:"7"},{name:"September --> October",value:"8"},{name:"October --> November",value:"9"},{name:"November --> December",value:"10"},{name:"December --> January",value:"11"},],
          		or_head:[],
          		month_fields:{text:"name",value:"text"}
			}
		},
		mounted() {
			this.carry_over_months.splice(new Date().getMonth())
			api.get(`${apiUrl}`+`head/head/get`)
		    .then((response) => {
		    	this.or_head = JSON.parse(JSON.stringify(response.data))
		      this.heads1 = this.list_to_tree_head(response.data)
		      this.heads2 = JSON.parse(JSON.stringify(this.heads1))
		      }).catch((err)=> {
        toast({
          type: VueNotifications.types.error,
          title: 'Network Error'
        })
      });
		},
		methods:{
			sendReqCarryOver() {
				api.post(`${apiUrl}budget/carryover/carryover/all/heads`,this.input2).then((res) => {
					toast({
									type: VueNotifications.types.success,
				                    title: 'Carry Over Complete'
								})
					this.warnModal = false
					this.confirmModal=false
								this.input2.from_month= {}

							}).catch((err)=> {
			        toast({
			          type: VueNotifications.types.error,
			          title: 'Network Error'
			        })
			      })
			},
			head1update(args,args2) {
				// console.log(args)
				// if(this.input.month) {
				// 	this.head1={}
				// 	for(var i=0;i<this.heads1.length;i++) {
				// 		if(args == this.heads1[i].id) {
				// 			this.head1.total = this.heads1[i].permissible_values[this.input.month]
				// 			this.head1.remaining = this.heads1[i].amount_left[this.input.month]
				// 			break;
				// 		}
				// 	}
							this.head1.total = args.permissible_values[this.input.source_month]
							this.head1.remaining = args.amount_left[this.input.source_month]
					// api.get(`${apiUrl}head/head/view/one/${this.input.source_head}`).then((resp) => {
					// 	this.head1.total = resp.data.permissible_values[this.input.month]
					// 	this.head1.remaining = resp.data.amount_left[this.input.month]
					// })
				// }
			},
			validateAmount(args) {
				if(args.value <=0) {
					this.input.amount = 1
				}
				if(args.value >this.head1.remaining) {
					this.input.amount = this.head1.remaining
				}
			},
			head2update(args,args2) {
				// if(this.input.month) {
				// 	for(var i=0;i<this.heads2.length;i++) {
				// 		if(args == this.heads2[i].id) {
				// 			this.head2.total = this.heads2[i].permissible_values[this.input.month]
				// 			this.head2.remaining = this.heads2[i].amount_left[this.input.month]
				// 			break;
				// 		}
				// 	}
				// }
				this.head2.total = args.permissible_values[this.input.destination_month]
				this.head2.remaining = args.amount_left[this.input.destination_month]
			},
			transferBudget() {
				this.$validator.validate().then(valid => {
		            
						api.post(`${apiUrl}transfer/budtrans/budget/monthly`,this.input).then((res)=> {
							console.log(res)
								toast({
									type: VueNotifications.types.success,
				                    title: 'Request Sent',
				                    message: 'Request for Budget Transfer is Sent'
								})
								this.input = {}
								this.$router.push(`/finance/budgettrans/list/all`)
						}).catch((err)=> {
        toast({
          type: VueNotifications.types.error,
          title: 'Network Error'
        })
      })
				
				})
			},
			carryOverConfirm() {
				this.warnModal = true
			},
			carryOver() {
				this.confirmModal = true
				var headlist = JSON.parse(JSON.stringify(this.or_head))
				var alteredhead = []
				console.log(this.or_head)
				for(var i=0;i<headlist.length;i++) {
					alteredhead.push({
						name:headlist[i].name,
						new_remaining_a : `${headlist[i].amount_left[parseInt(this.input2.from_month)]} - ${headlist[i].amount_left[parseInt(this.input2.from_month)]} = ${headlist[i].amount_left[parseInt(this.input2.from_month)] - headlist[i].amount_left[parseInt(this.input2.from_month)]}`,
						new_remaining_b : `${headlist[i].amount_left[parseInt(this.input2.from_month)+1]} + ${headlist[i].amount_left[parseInt(this.input2.from_month)]} = ${headlist[i].amount_left[parseInt(this.input2.from_month)+1] + headlist[i].amount_left[parseInt(this.input2.from_month)]}`,
						new_total_b : `${headlist[i].permissible_values[parseInt(this.input2.from_month)+1]} + ${headlist[i].amount_left[parseInt(this.input2.from_month)]} = ${headlist[i].permissible_values[parseInt(this.input2.from_month)+1] + headlist[i].amount_left[parseInt(this.input2.from_month)]}`
						// total_a:headlist[i].permissible_values[parseInt(this.input2.from_month)],
						// approved_a:headlist[i].permissible_values[parseInt(this.input2.from_month)] - headlist[i].amount_left[parseInt(this.input2.from_month)],
						// left_a:headlist[i].amount_left[parseInt(this.input2.from_month)],
						// total_b:headlist[i].permissible_values[parseInt(this.input2.from_month)+1],
						// approved_b:headlist[i].permissible_values[parseInt(this.input2.from_month)+1] - headlist[i].amount_left[parseInt(this.input2.from_month)+1],
						// left_b:headlist[i].amount_left[parseInt(this.input2.from_month)+1],
						// changed_left_a:0,
						// changed_total_b:headlist[i].permissible_values[parseInt(this.input2.from_month)] + headlist[i].permissible_values[parseInt(this.input2.from_month)],
						// changed_left_b:headlist[i].amount_left[parseInt(this.input2.from_month)] + headlist[i].amount_left[parseInt(this.input2.from_month)+1]
					})
				}
				this.alteredhead = alteredhead
				this.modaltitle = this.carry_over_months.filter((month) => {
					return month.value == this.input2.from_month
				})
				// api.post(`${apiUrl}budget/carryover/carryover/all/heads`,parseInt(this.input2.from_month)).then((res) => {
				// 	toast({
				// 					type: VueNotifications.types.success,
				//                     title: 'Carry Over Complete'
				// 				})
				// 				parseInt(this.input2.from_month) = {}

				// }).catch((err)=> {
    //     toast({
    //       type: VueNotifications.types.error,
    //       title: 'Network Error'
    //     })
    //   })
			},

			checkExistHead(node) {
		        for (var i=0; i < this.or_head.length; i++) {
		            if (this.or_head[i]._id == node)
		                return true;
		        }
		        return false;
		      },
			list_to_tree_head(list) {
	          var map = {}, node, roots = [], i;
	          for (i = 0; i < list.length; i += 1) {
	              map[list[i]._id] = i; // initialize the map
	              list[i].children = []; // initialize the children
	          }
	          for (i = 0; i < list.length; i += 1) {
	              node = list[i];
	              if (node.parent_head != undefined && this.checkExistHead(node.parent_head)) {
	                  // if you have dangling branches check that map[node.parentId] exists
	                  list[map[node.parent_head]].children.push(node);
	              } else {
	                  roots.push(node);
	              }
	          }
	          this.convertDataHead(roots)
	          return roots
	      },
	      convertDataHead(roots) {
	        for(var i=0;i<roots.length;i++) {
	            if(roots[i].children.length !=0) {
	              this.convertDataHead(roots[i].children);
	            }
	            roots[i].id = roots[i]._id;
	          roots[i].label = roots[i].name;
	        delete roots[i]._id;
	        delete roots[i].name;
	        if(roots[i].children.length<=0) {
	          delete roots[i].children
	        }
	        }
	      },
		},
		computed :{
			getMonthA(){
				return moment().month(parseInt(this.input2.from_month)).format("MMM")
			},
			getMonthB(){
				return moment().month(parseInt(this.input2.from_month)+1).format("MMM") 
			},
			getRemainingA() {
				return `Remaining of ${moment().month(parseInt(this.input2.from_month)).format("MMM")} - CarryOver Amount`
			},
			getRemainingB() {
				return `Remaining of ${moment().month(parseInt(this.input2.from_month)+1).format("MMM")} + CarryOver Amount`
			},
			getTotalB() {
				return `Total of ${moment().month(parseInt(this.input2.from_month)+1).format("MMM")} + CarryOver Amount`
			}
		}
	};
</script>
<style>
	#errors {
  color:red;
}
</style>
